import React, { Component } from 'react'
import Link from 'next/link'

import FooterSelectorOptionPrice from './FooterSelectorOptionPrice';
import FooterSelectorOptionDiscount from './FooterSelectorOptionDiscount';
import FooterSelectorOptionLocal from './FooterSelectorOptionLocal';

export default class FooterSelector extends Component {
    render() {
        return (
            <div id="footer-selector" className="container-fluid">
                <div className="container">
                    <div className="row">
                        <div className="col-xs-6">
                            <div className="row">
                                <div className="col-xs-1">
                                    <div className="footer-selector-type">
                                        TYPE
                                    </div>
                                </div>
                                <div className="col-xs-11 p-right-25">
                                    <div className="row">
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <a href="#">
                                                    <i className="icon icon-sedan"> </i>
                                                </a>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-suv"></i>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-convertible"></i>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-coupe"></i>
                                            </Link>
                                        </div>
                                    </div>
                                    <div className="row m-top-5">
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-hatchback"></i>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-wagon"></i>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-truck_2"></i>
                                            </Link>
                                        </div>
                                        <div className="col-xs-3">
                                            <Link href="#">
                                                <i className="icon icon-bus"></i>
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row m-top-30">
                                <div className="col-xs-1">
                                    <div className="footer-selector-make">
                                        MAKE
                                    </div>
                                </div>
                                <div className="col-xs-11 p-right-25">
                                    <div className="row">
                                        <div className="col-xs-2">
                                            <i className="icon icon-toyota"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-nisan"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-honda"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-isuzu"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-bmw"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-volkswagen"></i>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-xs-2">
                                            <i className="icon icon-sabaru"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-mazda"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-suzuki"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-ford"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-mercedes_benz"></i>
                                        </div>
                                        <div className="col-xs-2">
                                            <i className="icon icon-volvo"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-xs-2">
                            <b className="footer-selector-title">Car by Prices</b>
                            <FooterSelectorOptionPrice key={Math.random()}>Under US $1000</FooterSelectorOptionPrice>
                            <FooterSelectorOptionPrice key={Math.random()}>US $1,001 - $2,000</FooterSelectorOptionPrice>
                            <FooterSelectorOptionPrice key={Math.random()}>US $2,001 - $3,000</FooterSelectorOptionPrice>
                            <FooterSelectorOptionPrice key={Math.random()}>US $3,001 - $5,000</FooterSelectorOptionPrice>
                            <FooterSelectorOptionPrice key={Math.random()}>US $5,001 - $7,000</FooterSelectorOptionPrice>
                            <FooterSelectorOptionPrice key={Math.random()}>Over US $7,000</FooterSelectorOptionPrice>
                        </div>
                        <div className="col-xs-2">
                            <b className="footer-selector-title">Car by Discounts</b>
                            <FooterSelectorOptionDiscount key={Math.random()}>70% Off or More</FooterSelectorOptionDiscount>
                            <FooterSelectorOptionDiscount key={Math.random()}>60% Off or More</FooterSelectorOptionDiscount>
                            <FooterSelectorOptionDiscount key={Math.random()}>50% Off or More</FooterSelectorOptionDiscount>
                            <FooterSelectorOptionDiscount key={Math.random()}>40% Off or More</FooterSelectorOptionDiscount>
                            <FooterSelectorOptionDiscount key={Math.random()}>30% Off or More</FooterSelectorOptionDiscount>
                            <FooterSelectorOptionDiscount key={Math.random()}>1% - 30% Off</FooterSelectorOptionDiscount>
                        </div>
                        <div className="col-xs-2">
                            <b className="footer-selector-title">CFJ Local Services</b>
                            <FooterSelectorOptionLocal key={Math.random()} icon="icon icon-bahamas" >CFJ Bahamas</FooterSelectorOptionLocal>
                            <FooterSelectorOptionLocal key={Math.random()} icon="icon icon-kenya" >CFJ Kenya</FooterSelectorOptionLocal>
                            <FooterSelectorOptionLocal key={Math.random()} icon="icon icon-mozambique" >CFJ Mozambique</FooterSelectorOptionLocal>
                            <FooterSelectorOptionLocal key={Math.random()} icon="icon icon-malawi" >CFJ Malawi</FooterSelectorOptionLocal>
                            <FooterSelectorOptionLocal key={Math.random()} icon="icon icon-tanzania" >CFJ Tanzania</FooterSelectorOptionLocal>
                            <FooterSelectorOptionLocal key={Math.random()} icon="fa fa-globe-asia" >Show all countries</FooterSelectorOptionLocal>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
